section + section {
  margin-top: 100px;
  padding-top: 100px;
  border-top: 4px solid #ddd;
}

h1 {
  font-size: 3em;
  padding: 1em;
}

h2 {
  font-size: 2.5em;
  padding-left: 1em;
}

h2 + p {
  padding-left: 2em;
  font-size: 1.3em;
  letter-spacing: 0.1em;
}

.block {
  box-sizing: border-box;
  float: left;
  padding: 2em;
  border: 2px solid #ddd;
  margin: -2px 0 0 -2px;
  flex: 1 calc(100% / 4);
}

.block__wrapper {
  display: flex;
  flex-flow: row wrap;
  justify-content: flex-start;
}

.block__wrapper:after {
  display: block;
  clear: both;
  content: '';
}
